<template>
  <v-app>
    <!-- APPBAR -->
    <v-app-bar absolute top dense height="60">
      <v-container>
        <v-row>
          <v-col cols="12">
            <div class="d-flex align-center justify-space-between mr-5">
              <v-img alt="logo" class="shrink mr-2" style="filter: saturate(5)" contain src="./assets/logo.png" transition="scale-transition" height="60" />
              <v-toolbar-items>
                <v-divider class="mx-4" vertical></v-divider>
                <v-btn dark to="/home" text>Главная</v-btn>
                <v-divider class="mx-4" vertical></v-divider>
                <v-btn dark to="/adminmain" text>Главный админ</v-btn>
                <v-divider class="mx-4" vertical></v-divider>
                <v-btn dark to="/adminlocal" text>Админ на фигуре</v-btn>
              </v-toolbar-items>
              <div style="display: inline-block">
                {{ new Date().getDate() + " " + this.month[new Date().getMonth()] }} <br />
                {{ currentTime }}
              </div>
            </div>
          </v-col>
        </v-row>
      </v-container>
    </v-app-bar>

    <!-- CONTENT -->
    <v-main class="pa-0 pt-12">
      <v-alert v-if="isIE11" :value="isIE11" type="error">Внимание! Систему необходимо запускать в браузере Google Chrome. В Internet Explorer система работает некорректно</v-alert>
      <v-container class="pt-0" style="position: relative">
        <router-view />
      </v-container>
    </v-main>
    <!-- <v-footer padless>
      <v-container>
        <v-row class="text-center">
          <v-col cols="4">
            <p>© 2022 АО «Транснефть – Урал»</p>
          </v-col>
          <v-col cols="4">
            <p>Адрес</p>
          </v-col>
          <v-col cols="4">
            <p>Телефон</p>
          </v-col>
        </v-row>
      </v-container>
    </v-footer> -->
  </v-app>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "app",

  data() {
    return {
      currentTime: "",
      trav: null,
      // currentFoto: "../public/assets/i/topimg_start.png",
    };
  },

  computed: {
    ...mapState("nsi", ["month", "title", "navMenu"]),

    isIE11() {
      return !!window.MSInputMethodContext && !!document.documentMode;
    },
    getTime() {
      var d = new Date();

      return [d.getHours(), d.getMinutes(), d.getSeconds()]
        .map(function (x) {
          return x < 10 ? "0" + x : x;
        })
        .join(":");
    },
    getPath() {
      return this.$route.path == "/home";
    },
  },

  mounted() {
    // this.MUTATE_TITLE_TEXT("");
  },

  methods: {
    add() {
      console.log(this.titleText);
    },
    ...mapMutations("nsi", ["MUTATE_TITLE_TEXT"]),
  },
};
</script>

