import Vue from "vue";
import Vuex from "vuex";

import state from "@/store/state";
import getters from "@/store/getters";
import mutations from "@/store/mutations";
import nsi from "./modules/nsi";

Vue.use(Vuex);

const store = new Vuex.Store({
  // Общий стейт
  state,
  getters,
  mutations,

  modules: {
    nsi,
  },
});

export default store;
