import Vue from "vue";
import Router from "vue-router";
// import store from "@/store";

import HomeView from "@/views/HomeView";
import AdminLocalView from "@/views/AdminLocalView";

Vue.use(Router);

const router = new Router({
  routes: [
    { path: "/", redirect: "/home" },
    {
      path: "/home",
      name: "HomeView",
      component: HomeView,
      meta: {
        title: "Начальная страница",
      },
    },
    {
      path: "/adminlocal",
      name: "AdminLocalView",
      component: AdminLocalView,
      meta: {
        title: "Админы на формах",
      },
    },
  ],
});

