<template>
  <v-card-text>
    <v-row>
      <v-col cols="12" lg="4">
        <v-form class="mb-16 mt-4">
          <v-card class="pa-4">
            <v-card-title class="white--text">Информация о судье</v-card-title>
            <v-row>
              <v-col cols="12">
                <v-text-field v-model="editedItem.AUTOR_NAME" label="ФИО судьи" dense hide-details dark></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-select v-model="editedItem.NUM_FIGURE" :items="figure" label="Фигура" item-text="NAME" item-value="ID" dense hide-details dark></v-select>
              </v-col>
            </v-row>
          </v-card>
        </v-form>

        <v-form v-if="isEditForm">
          <v-card class="pa-4">
            <v-card-title class="white--text">Запись ошибок на фигурах</v-card-title>
            <v-row>
              <v-col cols="12">
                <v-select v-model="editedItem.NUM_PATH" :items="numPath" label="Номер заезда" dense hide-details dark></v-select>
              </v-col>
              <v-col cols="12">
                <v-select v-model="editedItem.PEOPLE_CODE" :items="peoples" item-value="ID" label="Участник" dense hide-details dark>
                  <template #[`selection`]="{ item }">
                    {{ item.ID + ": " + item.SECONDNAME + " " + item.NAME + " " + item.OTCH }}
                  </template>
                  <!-- ---------------------------Изменение элементов в списке--------------------------------- -->
                  <template #[`item`]="{ item }">
                    {{ item.ID + ": " + item.SECONDNAME + " " + item.NAME + " " + item.OTCH }}
                  </template>
                </v-select>
              </v-col>
              <v-col cols="12">
                <v-select v-model="editedItem.KOL_FINE" :items="numFine" label="Количество нарушений" dense hide-details dark></v-select>
              </v-col>
            </v-row>
            <v-card-actions>
              <v-btn @click="addFinePeople()" class="mt-4" color="success">Отправить</v-btn>
            </v-card-actions>
          </v-card>
        </v-form>
      </v-col>
      <v-col cols="12" lg="8">
        <pre class="white--text">{{ editedItem }}</pre>
      </v-col>
    </v-row>
  </v-card-text>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      editedItem: {},
    };
  },
  computed: {
    ...mapState("nsi", ["figure", "numPath", "peoples", "numFine"]),
    tableHeight() {
      return window.innerHeight - 120;
    },
    isEditForm() {
      if (this.editedItem.AUTOR_NAME && this.editedItem.NUM_FIGURE) {
        return true;
      }
    },
    // menu() {
    //   return this.navMenu.filter((item) => item.title != "Новости");
    // },
  },
  // filters: {
  //   menu() {
  //     return this.navMenu.filters((item) => item.title != "Новости");
  //   },
  // },
  mounted() {
    if (this.$cookies.get("NAME_AUTOR")) {
      this.$set(this.editedItem, "AUTOR_NAME", this.$cookies.get("NAME_AUTOR"));
    }
    if (this.$cookies.get("NUM_FIGURE")) {
      this.$set(this.editedItem, "NUM_FIGURE", Number(this.$cookies.get("NUM_FIGURE")));
      // this.$set(
      //   this.editedItem,
      //   "NUM_FIGURE",
      //   this.figure.find((i) => i.ID == this.$cookies.get("NUM_FIGURE"))
      // );
    }
  },
  methods: {
    addFinePeople() {
      console.log(this.editedItem);
      this.$cookies.set("NAME_AUTOR", this.editedItem.AUTOR_NAME, 60);
      this.$cookies.set("NUM_FIGURE", this.editedItem.NUM_FIGURE, 60);
    },
  },
};
</script>

